<template>
  <div class="container mt-5">
    <h3>Tracker Records</h3>
    <ul>
      <li>{{records[0].Tracker_name}}</li>
      <li>{{records[0].Tracker_id}}</li>
    </ul>
  </div> 
</template>

<script>
export default {
  name: 'HelloWorld',
  
 
  data(){
    return{
      records:[],
    }
  },
  methods: {
    getRecords(){
      fetch('http://127.0.0.1:8080/dashboard',{
        method: 'GET',
        headers: {
          "Content-Type" : "application/json"
        }
      })
      .then(resp => resp.json())
      .then(data => this.records.push(data))
      .catch(err => console.log(err))
    }
  },
  created(){
    this.getRecords()
  }
}
</script>

